"use client"

import { HeroSection } from "@/components/hero-section"
import { CapabilitiesSection } from "@/components/capabilities-section"
import { ApproachSection } from "@/components/approach-section"
import { ClientsSection } from "@/components/clients-section"
import { PhilosophySection } from "@/components/philosophy-section"
import { CTASection } from "@/components/cta-section"
import { Footer } from "@/components/footer"
import { CursorSpark } from "@/components/cursor-spark"
import { useScrollInversion } from "@/hooks/use-scroll-inversion"

export default function Home() {
  const invertedSections = useScrollInversion()

  return (
    <main className="min-h-screen">
      <CursorSpark />
      <HeroSection />
      <CapabilitiesSection inverted={invertedSections.has("capabilities")} />
      <ApproachSection inverted={invertedSections.has("approach")} />
      <ClientsSection inverted={invertedSections.has("clients")} />
      <PhilosophySection inverted={invertedSections.has("philosophy")} />
      <CTASection inverted={invertedSections.has("contact")} />
      <Footer />
    </main>
  )
}
